.widget-embed {
  display: block;
  width: 100%;

  &__placeholder {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 200px;
    background-color: #f5f5f5;
    border: 2px dashed #ddd;
    border-radius: 8px;

    span {
      font-size: 16px;
      color: #999;
    }
  }

  &__iframe {
    display: block;
    width: 100%;
    min-height: 400px;
    border: none;

    @media (max-width: 768px) {
      min-height: 300px;
    }
  }
}
